.gallery {
    margin-top: -20px;
    margin-left: -20px;


    display: flex;
    flex-wrap: wrap;

}
.photo-card {
    width: 100%;
    margin-top: var(--primary-margin);
    margin-left: var(--primary-margin);
    flex-basis: calc((100% - 80px) / 4);

    
    border: 1px solid #EEEEEE;
    border-top: 0;
    border-radius: 20px;
    overflow: hidden;
    transform: scale(0.9);
    transition: transform 500ms var(--transition-timing),
    box-shadow 500ms var(--transition-timing);

    background-color: #fff;

    &:hover,
    &:focus {
        transform: scale(1);

        box-shadow: 0px 1px 1px rgb(0 0 0 / 12%), 0px 4px 4px rgb(0 0 0 / 6%), 1px 4px 6px rgb(0 0 0 / 16%);
    }
}
.photo-card_img {
    width: 100%;
    height: 200px;
}

.info {
    padding: 10px;
    text-align: center;
    display: flex;
    justify-content: space-between;
}
.info-item {
    display: flex;
    flex-direction: column;
}
    
